  display: block;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  color: #424242;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #2c3e50;
  color: white;
  padding: 8px;
  z-index: 100;
  text-decoration: none;
}
 
.skip-link:focus {
   top: 0;
}

.header {
  min-height: 56px;
  transition: min-height 0.3s;
}

.header__inner {
  width: 100%;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.header__title {
  font-weight: 300;
  font-size: 4em;
  margin: 0.5em 0.25em;
  display: inline-block;
  color: #212121;
  font-family: 'Caveat', cursive;
  font-size: 60px;
}

.header__menu {
  font-size: 32px;
  margin: 10px auto;
  display: block;
  width: 30px;
  min-width: 44px;
  min-height: 44px;
  text-decoration: none;
}

.nav {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.nav__list {
  width: 100%;
  padding: 0;
  margin: 0;
}

.nav__item {
  box-sizing: border-box;
  display: inline-block;
  text-align: center;
  line-height: 24px;
  text-transform: uppercase;
  min-width: 44px;
  min-height: 44px;
}

.nav a {
  display: inline-block;
  padding: 1.3em;
  text-decoration: none;
  color: #616161;
}

.nav a:hover {
  color: #000;
  font-weight: bold;
}

.hero {
  display: flex;
  align-items: center;
  min-height: 380px;
  width: 100%;
  text-align: center;
  background-position: center;
  /* background-color: green; */
  position: relative;
}

.hero__background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.hero__inner {
  margin: 0 auto;
  max-width: 800px;
}

.hero__title {
  color: #fff;
  font-weight: 500;
  font-size: 36px;
}

.hero__tagline {
  color: #fff;
  margin-top: 16px;
  font-size: 18px;
  font-weight: 300;
}

@media (min-width: 1200px) {
  .hero {
    min-width: 1000px;
  }
}

.post {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 10px;
  row-gap: 0px;
  margin-left: 10px;
  margin-right: 10px;
}

.post-fav {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 10px;
  row-gap: 0px;
  margin-left: 10px;
  margin-right: 10px;
}

.post-item {
  margin: 16px 0;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
  padding: 16px 32px 32px 32px;
}

.post-item-image {
  width: 100%;
  height: 40vh;
  border-radius: 10px;
  margin-top: 15px;
  margin-bottom: 10px;
  object-fit: cover;
}

.post-item-name{
  margin-top: 10px;
  text-align: center;
}

.post-item-text{
  text-align: justify;
  margin-top: 5px;
}

.post-menu {
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;
  margin: 10px auto;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 40%;
  border-radius: 5px;
  overflow: hidden;
}

.meal-name{
  margin-top: 20px;
  text-align: center;
  font-size: large;
}

.meal-image {
  max-width: 90%; 
  height: 40vh;
  margin: 20px auto; 
  border-radius: 10px;
  object-fit: cover;
}

.headline-menu{
  text-align: center;
  margin: 20px;
  font-size: medium;
}

.menu-title{
  font-family: 'Caveat', cursive;
  font-size: 40px;
}

.headline-title{
  text-align: center;
  margin: 20px;
  font-size:40px;
  font-family: 'Caveat', cursive;
}

main {
  padding: 32px;
  flex: 1;
}
 
.content {
  margin: 0 auto;
  min-height: 100%;
}
 
.content .content__heading {
  font-weight: normal;
}
 
.movies {
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;
}
 
.movie-item {
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  overflow: hidden;
  margin-bottom: 10px;
}

.card-h3{
  display: inline-block;
  padding-top: 10px;
  min-width: 44px; 
  min-height: 44px; 
  font-size: 15px;
}
 
.movie-item__header {
  position: relative;
}
 
.movie-item .movie-item__header .movie-item__header__poster {
  width: 90%;
  height: 35vh;
  border-radius: 10px;
  object-fit: cover;
  margin: auto;
  margin-top: 20px;
}
 
.movie-item .movie-item__header .movie-item__header__rating {
  position: absolute;
  padding: 8px;
  bottom: 20px;
  left: 0;
  display: inline-block;
  background-color: black;
  color: white;
}
 
.movie-item .movie-item__header .movie-item__header__rating .movie-item__header__rating__score {
  margin-left: 10px;
}
 
.movie-item .movie-item__content {
  padding: 16px;
}
 
.movie-item .movie-item__content h3 {
  margin: 0 0 10px 0;
}
 
.movie-item .movie-item__content h3 a {
  color: #2c3e50;
  text-decoration: none;
}
 
.movie-item .movie-item__content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4; 
  -webkit-box-orient: vertical;
}

.link_button{
  min-width: 44px;
  min-height: 44px;
}

.detail {
  display: grid;
  grid-gap: 1.545em;
  font-size: 1em;
  margin-top: 1em;
}

.detail-img {
  width: 100%;
  height: 370px;
  object-fit: cover;
  object-position: center;
  border-radius: 5px;
}

.detail-name-address-rating {
  display: inline-block;
}

.icon-primary {
  color: var(--btn);
}

.detail h3 {
  font-size: 1.5em;
  padding: 1em 0 0 0;
  background-color: var(--primary-color);
  font-weight: bold;
}

.detail-desc {
  text-align: justify;
  color: white;
  margin-bottom: 0.5em;
  font-style: italic;
}

.category {
  padding: 0.3em 1em;
  color: var(--primary-color);
  background-color: var(--btn);
  margin-right: 2px;
  border: 1px solid transparent;
  border-radius: 0px 10px 0px 10px;
}

.detail-menu{
  border-radius: 5px;
  padding-bottom: 40px;
  background-color: #2c3e50;
  color: white;
  border-radius: 5px;
}

.detail-menu h3{
  color: white;
  text-align: center;
  font-family: 'Caveat', cursive;
  font-size: 30px;
  margin-bottom: 5px;
}

.detail-menu h4 {
  font-weight: bold;
  font-size: 1.37em;
  padding: 1em;
}

.detail-food {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-content: center;
  background-color: white;
  color: #000;
  border-radius: 5px;
  margin-bottom: 20px;
  margin-left: 20px;
  margin-right: 20px;
}

.detail-food li {
  padding: 0.5em 0;
  display: block;
  text-decoration: none;
  border-width: 0 0 0.5px;
}

.detail-drink {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-content: center;
  background-color: white;
  color: #000;
  border-radius: 5px;
  margin-left: 20px;
  margin-right: 20px;
}

.detail-drink li {
  padding: 0.5em 0;
  display: block;
  text-decoration: none;
  border-width: 0 0 0.5px;
}

.detail-info {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding-left: 0;
  margin-bottom: 0;
  background-color: #2c3e50;
  color: white;
  border-radius: 5px;
  padding: 20px;
}

.detail-info li {
  position: relative;
  display: block;
  padding: 0.6em;
  text-decoration: none;
}

.detail-review {
  max-width: 100%;
  font-size: 1em;
  text-align: center;
  background-color: #2c3e50;
  color: white;
  border-radius: 5px;
  padding: 20px;
}

.detail-review-item {
  margin-bottom: 1.5em;
  padding: 1em 1em;
  background-color: white;
  color: black;
  border-radius: 5px;
}

.detail-review-item:last-child {
  margin-bottom: 0;
}

.review-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em 1em;
  color: var(--btn);
}

.review-name {
  font-weight: bold;
  display: flex;
  align-items: center;
}

.review-date {
  font-size: 0.8em;
  font-weight: lighter;
}

.review-body {
  padding: 1em;
  text-align: left;
}

.give-review{
  text-align: center;
  margin-top: 20px;
  font-size: 17px;
  margin-bottom: 18px;
}

.submit-btn{
  background-color: #2c3e50;
  color: white;
  padding: 5px;
  border-radius: 10px;
}

#submit-review{
  min-height: 44px;
  min-width: 44px;
}

.form-review{
  border: 2px solid#2c3e50;
  border-radius: 5px;
  padding-top: 10px;
  padding: 20px;
}

.form-control {
  display: block;
  width: 100%;
  min-height: 1em;
  padding: 0.3em;
  font-size: 1em;
  font-weight: normal;
  background-color: #fff;
  appearance: none;
  outline: none;
  border: 1px solid transparent;
  min-height: 44px;
  min-width: 44px;
}

.form-label {
  padding-bottom: 10px;
  font-weight: bold;
  font-style: normal;
  font-size: small;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}

footer {
  background-color: #2c3e50;
  padding: 2em;
  width: 100%;
  text-align: center;
  color: white;
